import {
    LOGIN,
    SET_ABOUT_US,
    SET_AUTH_TOKEN,
    SET_CONTACT_US,
    SET_FOOTER,
    SET_FOOTER_DYNAMIC_DATA,
    SET_HEADER,
    SET_HOME_PAGE,
    SET_TOKEN,
} from "../Actions/Auth";
import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
    homepage: {},
    aboutUs: {},
    footer: {},
    header: {},
    footer_dynamic: {},
    contactUs: {},
    contactUsDynamic: {},
};
const dynamicContentReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_HOME_PAGE: {
            return {
                ...state,
                homepage: action.homepage,
            };
        }
        case SET_ABOUT_US: {
            return {
                ...state,
                aboutUs: action.aboutUs,
            };
        }
        case SET_FOOTER: {
            return {
                ...state,
                footer: action.footer,
            };
        }
        case SET_FOOTER_DYNAMIC_DATA: {
            return {
                ...state,
                footer_dynamic: action.footerDynamic,
            };
        }
        case SET_HEADER: {
            return {
                ...state,
                header: action.header,
            };
        }
        case SET_CONTACT_US: {
            return {
                ...state,
                contactUsDynamic: action.contactUsDynamic,
            };
        }
        default: {
            return state;
        }
    }
};

export default dynamicContentReducer;
